<template>
  <div>
    <h1></h1>

    <a-divider orientation="left"></a-divider>

    <a-modal title="" :visible="modalVisible" @ok="submit" :confirmLoading="loading" @cancel="handleCancel">
      <p></p>
    </a-modal>

    <a-form @submit="showModal" :autoFormCreate="(form)=>{this.form = form}">
      <a-form-item label='' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }" fieldDecoratorId="subject" :fieldDecoratorOptions="{rules: [{ message: 'Please input subject!' }]}">
        <a-input/>
      </a-form-item>

      <a-form-item label='' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }" fieldDecoratorId="raw" :fieldDecoratorOptions="{rules: [{ message: 'Please input subject!' }]}">
        <a-textarea placeholder="Basic usage" :rows="4" />
      </a-form-item>

      <!--  -->
      <a-divider orientation="left"></a-divider>
      <!--  -->
      <a-form-item label='' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }" fieldDecoratorId="mailBoxChosen" :fieldDecoratorOptions="{rules: [{ required:  false, message: '' }]}">
        <a-select mode="tags" placeholder="" :tokenSeparators="[',']">
          <a-select-option v-for="i in 0" :key="i.toString(1)">{{i}}</a-select-option>
        </a-select>
      </a-form-item>

      <a-form-item :wrapperCol="{ span: 12, offset: 5 }">
        <a-button :loading="loading" type='primary' htmlType='submit'>
          
        </a-button>
      </a-form-item>
    </a-form>
  </div>

</template>

<script>
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
const axios = require('axios')
const service = axios.create({
  baseURL: process.env.API_HOST,
  timeout: 5000,
  withCredentials: true
})
export default {
  name: 'emailPush',
  components: {
    mavonEditor
  },
  methods: {
    updateHtml: function (value, render) {
      this.raw = render
    },
    showModal () {
      this.modalVisible = true
    },
    handleCancel: function (e) {
      this.$message.info('')
      this.modalVisible = false
    },
    submit: function (e) {
      e.preventDefault()
      this.form.validateFields((err, values) => {
        if (!err) {
          this.loading = true
          console.log(values)
          service
            .post('/message/mail',
              {
                name: '',
                subject: values.subject,
                content: values.raw,
                toList: values.mailBoxChosen
              }
            )
            .then(function (response) {
              this.$message.success('')
            }.bind(this))
            .catch(function (error) {
              console.log(error)
              this.$message.error(error.response.data.message)
            }.bind(this))
            .then(function () {
              this.loading = false
            }.bind(this)
            )
        }
      })
      this.modalVisible = false
    }
  },
  data () {
    return {
      raw: '',
      loading: false,
      subject: '',
      modalVisible: false,
      toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: false, // 
        subscript: false, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: true, // 
        imagelink: false, // 
        code: true, // code
        table: true, // 
        fullscreen: true, // 
        readmodel: true, // 
        htmlcode: false, // html
        help: false, // 
        undo: true, // 
        redo: true, // 
        trash: false, // 
        save: false, // eventssave
        navigation: false, // 
        alignleft: false, // 
        aligncenter: false, // 
        alignright: false, // 
        subfield: true,
        preview: true
      }
    }
  }
}
</script>

<style>
</style>



